       h2 2.2 Requisitos para la selección y adquisición de medicamentos y dispositivos médicos

    p.mb-5 Los estándares de calidad para medir los procesos de selección y adquisición de medicamentos y dispositivos médicos están regidos por el Decreto 780 del 2016 y la Resolución 1403 del 2007, por lo que la forma de dar cumplimiento total a la normatividad es realizar auditorías internas y autoevaluaciones de verificación y control a la luz de esta para detectar a tiempo cualquier irregularidad que se pueda estar presentando.

    .row.justify-content-center.align-items-center.mb-5
