    p.mb-5(data-aos="fade-left") En cuanto al archivo también se debe concretar por necesidad y complejidad el tiempo que se tendrán los documentos archivados, la mayor parte de estos se dejan máximo dos años, pero puede haber algunos que sea necesario tenerlos a disposición hasta por 20 años.

